"use client";
import React, { useRef, useEffect, useState } from "react";
import Image from "next/image";
import { yazilar } from "../../content/yazilar";
import Navbar from "../../src/components/Navbar";

export default function Yazilar() {
  const cardRefs = useRef<(HTMLDivElement | null)[]>([]);
  const titleRef = useRef<HTMLHeadingElement>(null);
  const [expandedYazi, setExpandedYazi] = useState<string | null>(null);
  const [selectedImage, setSelectedImage] = useState<string | null>(null);

  useEffect(() => {
    if (titleRef.current) {
      titleRef.current.style.opacity = '0';
      titleRef.current.style.transform = 'translateY(-20px)';
      
      setTimeout(() => {
        if (titleRef.current) {
          titleRef.current.style.transition = 'all 0.8s ease-out';
          titleRef.current.style.opacity = '1';
          titleRef.current.style.transform = 'translateY(0)';
        }
      }, 100);
    }
  }, []);

  const toggleYazi = (slug: string) => {
    setExpandedYazi(expandedYazi === slug ? null : slug);
    setSelectedImage(null);
  };

  return (
    <>
      <Navbar />
      <main style={{ maxWidth: 1440, margin: '0 auto', padding: '2.5rem 1rem' }}>
        <h1 ref={titleRef} style={{
          fontSize: '2.7rem',
          marginBottom: 18,
          fontWeight: 800,
          color: '#111',
          letterSpacing: 0.5,
          fontFamily: 'Playfair Display, serif',
          textShadow: '0 2px 12px #0070f322',
          textAlign: 'center',
          lineHeight: 1.1,
        }}>
          Yazılarım
        </h1>
        <div style={{
          width: '100%',
          height: 4,
          background: '#000',
          borderRadius: 2,
          marginBottom: 24,
          maxWidth: 180,
          margin: '0 auto 24px auto',
        }} />
        <p style={{
          textAlign: 'center',
          fontSize: '1.1rem',
          color: '#444',
          maxWidth: 800,
          margin: '0 auto 38px auto',
          lineHeight: 1.6,
        }}>
          Okul kapsamında ve hobi olarak uğraştığım yazılara ve küçük projeleri bu sekmede inceleyebilirsiniz.
        </p>
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          gap: 36,
          width: '100%',
          minHeight: 400,
        }}>
          {yazilar.map((yazi, idx) => (
            <div
              key={yazi.slug}
              ref={el => cardRefs.current[idx] = el}
              style={{
                background: '#fff',
                borderRadius: 22,
                boxShadow: '0 8px 40px rgba(0, 0, 0, 0.15)',
                display: 'flex',
                flexDirection: 'row',
                cursor: 'pointer',
                overflow: 'hidden',
                transition: 'all 0.3s ease-in-out',
                height: 260,
                maxWidth: 1100,
                margin: '0 auto',
                position: 'relative',
                zIndex: expandedYazi === yazi.slug ? 10 : 1,
                transform: expandedYazi === yazi.slug ? 'scale(1.02)' : 'scale(1)',
              }}
              onMouseEnter={e => {
                if (expandedYazi !== yazi.slug) {
                  e.currentTarget.style.transform = 'scale(1.02)';
                  e.currentTarget.style.boxShadow = '0 16px 48px rgba(0, 0, 0, 0.25)';
                }
              }}
              onMouseLeave={e => {
                if (expandedYazi !== yazi.slug) {
                  e.currentTarget.style.transform = 'scale(1)';
                  e.currentTarget.style.boxShadow = '0 8px 40px rgba(0, 0, 0, 0.15)';
                }
              }}
              onClick={() => toggleYazi(yazi.slug)}
            >
              <div style={{ width: 400, height: 260, background: '#f5f5f5', display: 'flex', alignItems: 'center', justifyContent: 'center', flexShrink: 0 }}>
                <Image
                  src={yazi.image}
                  alt={yazi.title}
                  width={400}
                  height={260}
                  style={{ objectFit: 'contain', width: '100%', height: '100%' }}
                />
              </div>
              <div style={{ flex: 1, display: 'flex', flexDirection: 'column', justifyContent: 'space-between', padding: '2rem 1.7rem 2.2rem 1.7rem', rowGap: 18, overflowY: 'auto' }}>
                <div>
                  <h2 style={{ fontSize: '1.38rem', marginBottom: 14, fontWeight: 600, letterSpacing: 0.2, lineHeight: 1.3 }}>{yazi.title}</h2>
                  <p style={{ color: '#444', fontSize: '1.05rem', marginBottom: 20, lineHeight: 1.7, letterSpacing: 0.01 }}>{yazi.description}</p>
                </div>
                <div style={{ color: '#888', fontSize: '1.08rem', marginTop: 'auto', letterSpacing: 0.01, lineHeight: 1.5 }}>
                  {yazi.date}
                  {yazi.hazirlayanlar && (
                    <>
                      <span style={{ margin: '0 6px' }}>•</span>
                      <span>{yazi.hazirlayanlar}</span>
                    </>
                  )}
                </div>
                <p style={{ marginTop: '1rem', fontSize: '1.05rem', color: '#666', textAlign: 'center' }}>
                  {expandedYazi === yazi.slug ? 'Detayları Gizle' : 'Detaylar için tıklayınız >>'}
                </p>
              </div>

              {/* Genişleyen Detay Bölümü */}
              {expandedYazi === yazi.slug && (
                <div style={{
                  position: 'fixed',
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                  background: 'rgba(0, 0, 0, 0.85)',
                  zIndex: 100,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  padding: '2rem',
                  overflowY: 'auto',
                  animation: 'fadeIn 0.3s ease-out'
                }}>
                  <div style={{
                    background: '#fff',
                    borderRadius: 22,
                    padding: '2rem',
                    maxWidth: '90%',
                    maxHeight: '90vh',
                    overflowY: 'auto',
                    position: 'relative',
                    boxShadow: '0 20px 60px rgba(0, 0, 0, 0.3)',
                    animation: 'scaleIn 0.3s ease-out'
                  }}>
                    <button
                      onClick={(e) => {
                        e.stopPropagation();
                        toggleYazi(yazi.slug);
                      }}
                      style={{
                        position: 'absolute',
                        top: '1rem',
                        right: '1rem',
                        background: 'none',
                        border: 'none',
                        fontSize: '1.5rem',
                        cursor: 'pointer',
                        color: '#666',
                        padding: '0.5rem',
                        borderRadius: '50%',
                        width: '40px',
                        height: '40px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        transition: 'background-color 0.2s',
                      }}
                      onMouseEnter={e => e.currentTarget.style.backgroundColor = '#f5f5f5'}
                      onMouseLeave={e => e.currentTarget.style.backgroundColor = 'transparent'}
                    >
                      ×
                    </button>
                    <div style={{ padding: '1rem' }}>
                      <h3 style={{ fontSize: '1.4rem', fontWeight: 600, marginBottom: '1.5rem' }}>{yazi.title}</h3>
                      <p style={{ color: '#444', fontSize: '1.1rem', lineHeight: 1.7, marginBottom: '2rem' }}>
                        {yazi.details || yazi.description}
                      </p>

                      {/* Fotoğraf Galerisi */}
                      {yazi.gallery && yazi.gallery.length > 0 && (
                        <div style={{ marginTop: '2rem' }}>
                          <h4 style={{ fontSize: '1.2rem', fontWeight: 600, marginBottom: '1rem' }}>Yazı Görselleri</h4>
                          <div style={{ 
                            display: 'grid', 
                            gridTemplateColumns: 'repeat(auto-fill, minmax(250px, 1fr))',
                            gap: '1.5rem',
                            marginBottom: '1rem'
                          }}>
                            {yazi.gallery.map((image, index) => (
                              <div
                                key={index}
                                onClick={(e) => {
                                  e.stopPropagation();
                                  setSelectedImage(image);
                                }}
                                style={{
                                  cursor: 'pointer',
                                  borderRadius: '12px',
                                  overflow: 'hidden',
                                  boxShadow: '0 4px 12px rgba(0, 0, 0, 0.1)',
                                  transition: 'transform 0.2s',
                                }}
                                onMouseEnter={e => e.currentTarget.style.transform = 'scale(1.05)'}
                                onMouseLeave={e => e.currentTarget.style.transform = 'scale(1)'}
                              >
                                <Image
                                  src={image}
                                  alt={`${yazi.title} - Görsel ${index + 1}`}
                                  width={300}
                                  height={200}
                                  style={{ width: '100%', height: '200px', objectFit: 'cover' }}
                                />
                              </div>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>

        {/* Büyük Görsel Modalı */}
        {selectedImage && (
          <div
            onClick={() => setSelectedImage(null)}
            style={{
              position: 'fixed',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              background: 'rgba(0, 0, 0, 0.9)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              zIndex: 1000,
              cursor: 'pointer',
              animation: 'fadeIn 0.2s ease-out'
            }}
          >
            <div style={{ maxWidth: '90vw', maxHeight: '90vh' }}>
              <Image
                src={selectedImage}
                alt="Büyük görsel"
                width={1200}
                height={800}
                style={{ 
                  width: 'auto',
                  height: 'auto',
                  maxWidth: '100%',
                  maxHeight: '90vh',
                  objectFit: 'contain'
                }}
              />
            </div>
          </div>
        )}

        <style jsx global>{`
          @keyframes fadeIn {
            from { opacity: 0; }
            to { opacity: 1; }
          }
          @keyframes scaleIn {
            from { transform: scale(0.95); opacity: 0; }
            to { transform: scale(1); opacity: 1; }
          }
        `}</style>
      </main>
    </>
  );
} 